<template>
    <div>
      <Header>购物车</Header>
      <ul class="content">
        <li v-for="(item,index) in $store.state.cart">
          <img :src="item.bookPic">
          <div>
            <h5>{{item.bookName}}</h5>
            <b>￥{{item.bookPrice}}</b>
            <p>
              <button @click="reduce(item.bookId)">-</button>数量：{{item.bookCount}}<button @click="add(item.bookId)">+</button>
            </p>
            <p>小计：{{item.bookCount * item.bookPrice | my}}</p>
            <button @click="remove(item.bookId)">删除</button>
          </div>
        </li>
        <b>总计：{{$store.getters.total | my}}</b><button @click="clear">清空购物车</button>
      </ul>
    </div>
</template>

<script>
    import Header from '../base/Header';
    import * as Types from '../store/mutations-type';

    export default {
        data() {
            return {
              msg: 2.5678
            }
        },
        filters: {
          my: function (val) {
            return val.toFixed(2);
          }
        },
        methods: {
          clear() {
            this.$store.commit(Types.CLEAR);
          },
          remove(id) {
            this.$store.commit(Types.REMOVECART, id);
          },
          reduce(id) {
            this.$store.commit(Types.REDUCECOUNT, id)
          },
          add(id) {
            this.$store.commit(Types.ADDCOUNT, id)
          }
        },
        computed: {},
        components: {Header},
    }
</script>

<style scoped lang="less">
.content{
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;
  margin-bottom: 60px;
  li{
    display: flex;
    flex-direction: row;
    padding: 20px 0;
    border-bottom: 1px solid #e1e1e1;
    button{
      display: inline-block;
      width: 50px;
    }
  }
  b{
    margin-right: 200px;
  }
}
</style>



